/* Общие стили для всех тем */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
}

.menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* Всегда 2 колонки */
    gap: 16px;
    padding: 20px;
    justify-content: center;
}

.menu-element {
    border-radius: 10px;
    padding: 10px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.emoji {
    font-size: 40px;
}

.details {
    margin-top: 10px;
}

.name {
    font-size: 16px;
    margin: 0;
}

.price {
    font-size: 14px;
}

/* Кнопка действий */
.action-button {
    border: none;
    padding: 10px 20px;
    margin-top: 10px;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
}

/* Тема светлая */
@media (prefers-color-scheme: light) {
    body {
        background-color: #ffffff;
        color: #000000;
    }

    .menu-element {
        background-color: #f0f0f0;
    }

    .price {
        color: #555555;
    }

    .action-button {
        background-color: #007bff;
        color: #ffffff;
    }

    .action-button:hover {
        background-color: #0056b3;
    }
}

/* Тема темная */
@media (prefers-color-scheme: dark) {
    body {
        background-color: #000000;
        color: #ffffff;
    }

    .menu-element {
        background-color: #1c1c1c;
    }

    .price {
        color: #cccccc;
    }

    .action-button {
        background-color: #ff9900;
        color: #000000;
    }

    .action-button:hover {
        background-color: #ff7711;
    }
}
